.w-100 {
    width: 100% !important;
}

.h-100 {
    height: 100% !important;
}

.mt-2 {
    margin-top: 0.5rem !important;
}

ul {
    list-style: none;
}

body {
    font-family: 'Roboto';
    background: $main;
}

::after,
::before {
    font-family: 'fontawesome';
}

.container {
    @include container();
}

.row {
    @include row();
}

.d-flex {
    display: flex;
}

.flex-wrap {
    flex-wrap: wrap !important;
}

.d-none {
    display: none !important;
}

.d-md-flex {
    display: flex !important;
}

.flex-column {
    flex-direction: column;
}

.justify-center {
    justify-content: center;
}

.justify-content-end {
    justify-content: flex-end !important;
}

.aline-center {
    align-items: center;
}

.hidden {
    visibility: hidden;
}



.bdr-7 {
    border-radius: 7px;
}

.icon {
    &-film::before {
        content: '\f008';
        color: $white;
        font-weight: 300;
    }

    &-play::before {
        content: '\f04b';
        color: $white;
        font-weight: 900;
        position: absolute;
    }

    &-play::after {
        content: '\10f04b';
        color: $white;
        font-weight: 900;
        opacity: 0.4;
    }

    &-burger::before {
        content: '\f0c9';
    }

    &-checkMark::before {
        content: '\f2f7';
    }

    &-leftArrow::before {
        content: "\f060";
    }

    &-rightArrow::before {
        content: "\f061";
    }

    &-long-leftArrow::before {
        color: #000;
        content: '\f0a8';
    }

    &-long-rightArrow::before {
        color: #000;
        content: '\f0a9';
    }

    &-look::before {
        content: '\f06e';
        display: flex;
        font-size: 38px;
    }

    &-brain::before {
        font-size: 50px;
        color: rgb(118, 162, 243);
        content: '\f5dc';
    }

    &-registration::before {
        content: '\f2bb';
        font-size: 50px;
        color: rgb(118, 162, 243);
    }
}

.btn {
    font-family: "Roboto";
    font-weight: 500;
    font-size: 14px;
    line-height: 12px;
    padding: 17px 48px;
    background: $purple;
    border-radius: 4px;
    color: $black;
    margin-right: 30px;
    display: inline-block;

    &:hover {
        background: $main;
        color: $white;
    }
}

.title {
    text-align: center;
    font-size: 36px;
    font-weight: 400;
    margin-bottom: 30px;
    display: flex;

    &__link {
        color: $titles;
        margin: auto;

        &::after {
            content: '';
            height: 2px;
            background: $titles;
            display: block;
            margin-top: 2px;
        }
    }
}

.js-btn-top {
    position: fixed;
    bottom: -100%;
    right: 10px;
    transition: 0.5s;
    padding: 10px 20px;
    z-index: 999;

    &:hover {
        background: $nav-link;
    }
}

.js-btn-top.show {
    bottom: 30px;
}