.video {
    margin-top: 10px;
    width: 100%;
    color: $white;
    margin-bottom: 40px;

    &__mainVideo {
        position: relative;
        height: 450px;
        margin-top: 30px;
        margin-bottom: 30px;

        @include media($sm) {
            margin-bottom: 30px;
        }
    }

    &__films {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 400px;
        gap: 30px;

        @include media($sm) {
            grid-template-columns: repeat(1, 1fr);
        }

        &-second {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 30px;

            @include media($sm) {
                height: 400px;
                gap: 15px;
            }
        }
    }

    &__film {
        position: relative;

        &-content {
            position: relative;
            background: $series;
            height: 100%;
            border-radius: 7px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
}

#videos {
    margin-top: 2rem;
    background: $second-main;
    padding-top: 20px;
    padding-bottom: 10px;
}

.mainVideo {
    &__content {
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &>iframe {
            border-radius: 5px;
        }
    }
}

.shorts {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin-bottom: 2rem;

    @include media($xs) {
        flex-wrap: wrap;
    }

    @include media(370px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
    }

    &>* {
        max-width: 250px;
        width: 100%;
        height: 400px;
        margin-left: 10px;
        margin-right: 10px;
        box-shadow: 0 1px 8px #888888;

        @include media($lg) {
            max-width: 200px;
            height: 300px;
        }

        @include media($md) {
            max-width: 150px;
        }

        @include media($sm) {
            max-width: 120px;
            height: 250px;
        }

        @include media($xs) {
            max-width: 200px;
            height: 300px;
            margin-bottom: 20px;
        }

        @include media(476px) {
            max-width: 150px;
            height: 250px;
        }

        @include media(370px) {
            margin: 0;
        }
    }
}