$Fonts: ('Montserrat', 'Regular', 400), 
        ('Montserrat', 'Bold', 700),
        ('Comfortaa', 'Regular', 400),
        ('Comfortaa', 'Bold', 700),
        ('fontawesome', 'duotone', 900),
        ('fontawesome', 'light', 300),
        ('Roboto', 'Medium', 500),
        ('Roboto', 'Regular', 400);

@each $family, $name, $weight in $Fonts {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }    
}

$white: #ffffff;

$black: #000000;

$ads: linear-gradient(90.09deg, #2a773d 0%, #1d5557 70.52%, #47909d 100%, #743a88 100%);

$nav: rgba(1, 3, 5, 0.8);

$nav-link: #dfa62d;

$orange: #F18128;

$banner: rgba(7, 15, 25, 0.75);

$grey: #C8C8C8;

$purple: #9e59c5;

$titles: #9e59c5;

$main: #d3eaf7;

$second-main: #8fd7e427;

$way: linear-gradient(180deg, rgba(126, 10, 116, 0.651) 0%, rgba(17, 34, 70, 0.548) 51.04%, rgba(7, 59, 123, 0.637) 100%);

$film: linear-gradient(180deg, rgba(241, 129, 40, 0.35) 0%, rgba(0, 0, 0, 0.71) 51.04%, rgba(7, 15, 25, 0.8) 100%);

$series: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);

$footer: #111011;