.progress-bar {
    width: 100%;
    height: 3px;
    background: rgb(56, 53, 33);

    display: flex;
    align-items: center;

    margin-top: 2rem;
    margin-bottom: 2rem;

    &-state {
        height: 100%;
        background: $nav-link;
    }

    &-circle {
        width: 15px;
        height: 15px;
        background: $nav-link;  
        border-radius: 50%;
    }
}

#weeks {
    margin-top: 5rem;
}

.weeks {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    @include media($md) {
        grid-template-columns: repeat(2, 1fr);
    }

    @include media($xs) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.week {
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    text-align: center;

    &__content {
        position: relative;
        background: rgba(81, 69, 116, 0.527);
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $white;
        padding: 26px 15px 26px 15px;
        height: 230px;
        font-family: "Montserrat";
    }
    
    &__desc {
        font-family: "Roboto";
        font-size: 14px;
        line-height: 21px;
        @include limit(6);
    }
}