.menu {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .menu {
    position: absolute;
    right: 0;
    background: rgba(1, 3, 5, 0.8);
    flex-direction: column;
    min-width: 200px;
    border-radius: 0 0 5px 5px;
    transform: scaleY(0);
    transition: 0.5s;
    transform-origin: 50% 0 0;
    opacity: 0;
  }
  .menu.active {
    opacity: 1;
    transform: scaleY(1);
  }
}

.menu__link {
  font-family: "Montserrat";
  font-size: 14px;
  padding: 43px 6px 40px 6px;
  display: block;
  margin-left: 18px;
  transition: 0.5s;
  position: relative;
}

@media (max-width: 768px) {
  .menu__link {
    padding: 20px;
    margin-left: 0;
  }
}

.menu__link::after {
  content: '';
  background: #dfa62d;
  height: 2px;
  width: 0;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transition: 0.5s;
  border-radius: 2px 2px 0px 0px;
}

@media (max-width: 768px) {
  .menu__link::after {
    width: 2px;
    height: 0;
    left: 0;
    bottom: 50%;
  }
}

.menu__link:hover {
  color: #dfa62d;
}

@media (max-width: 768px) {
  .menu__link:hover {
    background: rgba(223, 166, 45, 0.1);
  }
}

.menu__link:hover::after {
  width: 100%;
  left: 0;
}

@media (max-width: 768px) {
  .menu__link:hover::after {
    bottom: 0;
    width: 2px;
    height: 100%;
  }
}

