.ways {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    @include media($md) {
        grid-template-columns: repeat(2, 1fr);

    }

    @include media($xs) {
        grid-template-columns: repeat(1, 1fr); 
    }
}
#ways {
    margin-top: 5rem;
}
.way {
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    text-align: center;

    &__test {
        margin: auto;
    }

    &__image {
        object-fit: cover;
        height: 70px;
        margin-bottom: 20px;
    }

    &__content {
        position: relative;
        background: $way;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $white;
        padding: 26px 15px 20px 15px;
        height: 220px;
        font-family: "Montserrat";

        @include media($sm) {
            height: 235px;
        }

        @include media($xs) {
            height: 220px;
        }
    }
    
    &__desc {
        font-family: "Roboto";
        font-size: 14px;
        line-height: 21px;
        @include limit(6);
    }
}

.way__test {
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    @include media($md) {
        justify-content: space-between;
    }

    @include media($xs) {
        height: 430px;
        flex-direction: column;
    }

    &-content {
        @include media($xs) {
            margin-bottom: 30px;
        }
    }

    &-link {
        margin-top: 1rem;
        color: rgb(255, 226, 249);
        text-decoration: underline !important;

        &:hover {
            color: rgb(248, 187, 235);
        }
    }

    &-icon {
        margin-bottom: 1rem; 
    }
}