.book-hover-bg {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
    color: white;
    font-size: 48px;
    width: 100%;
    height: 100%;
    opacity: 0;
    box-shadow: inset 3500px 3500px rgba(32, 22, 0, 0.424);
    
    &:hover {
        opacity: 1;
    }
}

.book-hover-icon {
    height: 40px;
    width: 40px;
    z-index: 30;
}

.book-list {
    position: relative;
    box-shadow: 0 1px 8px #888888;
}

#books {
    margin-top: 2rem;
}

.book-arrow {
    @include media(465px) {
        width: 25px !important;
    }
}

.books-view {
    height: 330px;

    @include media($lg) {
        height: 310px !important;
    }

    @include media($sm) {
        height: 250px !important;
    }

    @include media($xs) {
        height: 200px !important;
    }

    @include media(465px) {
        height: 170px !important;
    }

    @include media(365px) {
        width: 100% !important;
        height: 150px !important; 
    }
}

.books {
    @include media(365px) {
        margin: 0;
        margin-bottom: 20px;
    }
}