.rooms {
  margin-top: 2rem;
  
  &>div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__comment {
    margin-top: 30px;
    margin-bottom: 30px;

    & > * {
      font-family: 'Montserrat';
      background: $way;
      padding: 30px;
      border-radius: 40px;

      @include media(410px) {
        padding: 20px;
        border-radius: 40px;
        font-size: 14px;
      }
      @include media(350px) {
        padding: 15px;
        border-radius: 30px;
        font-size: 13px;
      }

      &:hover{
        cursor: pointer;
      }
    }
  }
}

.room {

  &__youtube {
    width: 80%;
    height: 480px;
    margin-bottom: 2rem;

    @include media($md) {
      height: 320px;
    }

    @include media($sm) {
      height: 250px;
    }

    @include media(488px) {
      height: 200px;
    }
  }
}

$multi-carousel-height: 400px;
$multi-carousel-preview-width: 70;

$products-amount-visible: 5;
$product-width: 200px;

$multi-carousel-width: calc((200px*5) + 10px);

.multi-carousel {
  margin: 20px;
  margin-top: 0;

  &__view {
    width: 1010px;
    height: $multi-carousel-height;
    background: white;
    box-shadow: 0 1px 8px #888888;
    margin: auto;
    overflow: hidden;
    position: relative;

    @include media($lg) {
      width: 848px;
    }

    @include media($md) {
      width: 700px;
      height: 350px;
    }

    @include media($sm) {
      width: 548px;
      height: 300px;
    }

    @include media($xs) {
      width: 400px;
      height: 220px;
    }

    @include media(465px) {
      width: 300px;
      height: 180px;
    }
  }

  &__control {
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 70px;
    background-color: rgba(#ddd, .6);
    transition: background-color .3s;
    display: table;

    @include media($sm) {
      width: 40px;
    }

    .icon {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
    }

    &:hover {
      background-color: rgba(#ddd, .8);
    }

    &:first-of-type {
      left: 0;
    }

    &:last-of-type {
      right: 0;
    }
  }
}

.product-list {
  position: absolute;
  margin: 0;
  padding: 0;
  transition: transform .3s;
  transform: translateX(0px);
  list-style: none;
  height: 100%;

  &__item {
    width: 200px;
    height: 100%;
    display: inline-block;

    @include media($lg) {
      width: 212px;
    }

    @include media($md) {
      width: 175px;
    }

    @include media($sm) {
      width: 137px;
    }

    @include media($xs) {
      width: 100px;
    }

    @include media(465px) {
      width: 75px;
    }
  }
}

.product {
  display: table;
  height: 100%;
  width: 100%;
  object-fit: cover;

  span {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    color: white;
    font-size: 50px;
  }
}