.faq {
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
}

#steps {
    margin-top: 5rem;
    background: $second-main;
    padding-top: 20px;
    padding-bottom: 20px;
}

.step {
    margin-top: 10px;
    width: 100%;
    color: #495057;
    text-align: center;
    position: relative;
    display: block;
    padding: 0.75rem 1.25rem;
    margin-bottom: -1px;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.125);


    &:hover,
    &:focus {
        color: #495057;
        text-decoration: none;
        background-color: #f8f9fa;
        z-index: 1;
    }

    &:active {
        color: #212529;
        background-color: #e9ecef;
    }

    &:first-child {
        border-top-left-radius: 0.25rem;
        border-top-right-radius: 0.25rem;
    }

    &:last-child {
        margin-bottom: 0;
        border-bottom-right-radius: 0.25rem;
        border-bottom-left-radius: 0.25rem;
    }

    &.active {
        z-index: 2;
        color: #fff;
        background-color: #1d37ccb6;
        border-color: #007bff;
    }
}

.answer {
    position: relative;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 10px solid rgba(35, 35, 131, 0.199);
    border-radius: 0.25rem;
    display: none;
    padding: 1rem;

    &__list {
        list-style: circle;
        list-style-type: disc;
        list-style-position: inside;
    }
}