.footer {
    background: $footer; 
    display: flex;

    &__map {
        width: 300px;
        height: 230px;

        @include media($md) {
            width: 80%;
        }
    }

    &__title {
        text-align: center;
        font-size: 24px;

        @include media($xs) {
            margin: auto;
            margin-bottom: 30px; 
        }

        @include media($md) {
            margin-bottom: 2rem;
        }

        @include media(488px) {
            font-size: 20px;
            margin-bottom: 1rem;
        }
    }

    &__content {
        color: $white;
        font-size: 18px;
        padding: 30px;

        display: flex;
        flex-direction: column;
        align-items: center; 
        justify-content: center;

        @include media($xs) { 
            padding: 15px;
        }
    }

    &__info {
        margin-top: 2rem;
        width: 100%;
        display: flex;
        justify-content: space-between;

        @include media($md) {
            flex-direction: column-reverse;
            align-items: center;
        }
    }

    &__contacts {
        width: 60%;
        display: flex;
        flex-direction: column;
        align-items: center;

        &-offSite {
            text-align: center;
            margin-top: 1rem;

            & > a {
                color: white;

                &:hover {
                    color: aqua;
                }
            }
        }

        @include media($md) {
            width: 100%;
            margin-bottom: 2rem;
        }

        @include media(488px) {
            font-size: 14px;
        }
    }
}

.contact {

    &__title {
        font-size: 20px;
        margin-bottom: 2rem;

        @include media($md) {
            margin-bottom: 1rem;
        }
    }

    &__columns {
        display: flex;
        justify-content: center;

        @include media(400px) {
            flex-direction: column;
            align-items: center; 
            text-align: center;
        }
    }

    &__list {

        &-links {
            @include media(400px) {
                margin-top: 1rem;
            }
        }

        &-links * {
            color: $white;
            padding-left: 25px;

            &:hover {
                color: aqua;
            }

            @include media(400px) {
                padding-left: 0;
            }
        }
    }
}