.ads {
    background: rgba(88, 65, 121, 0.7);

    & > div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: $white;

        @include media($sm) {
            display: flex;
            flex-direction: column;
        }
    }

    &__info {
        display: flex;
        align-items: center;
    }

    &__languages {
        display: flex;
    }

    &__language {
        color: $white;
        text-align: center;
    }

    &__slogan {
        text-transform: uppercase;
        font-size: 16px;

        @include media($sm) {
            text-align: center;
            margin-top: 1.5rem;
        }
    }

    &__search {
        max-width: 120px;
        height: 30px;
        width: 100%;
        margin-left: 30px;
    }
}